
import { defineComponent } from "vue";
export default defineComponent({
  name: "Form",
  props: {},
  data() {
    return {
      artists: "" as string,
      num_songs: "" as string,
      request: "default" as string,
    };
  },
  updated() {
    this.genRequest();
  },
  methods: {
    genRequest() {
      var artists_arr: Array<string> = this.artists.split(",");
      for (let i in artists_arr) {
        artists_arr[i] = artists_arr[i].trim();
      }
      let arr_json: string = encodeURIComponent(JSON.stringify(artists_arr));
      var request: string = "https://jkcraft.duckdns.org:14500/lyrics?";
      request += "artists=" + arr_json;
      if (this.num_songs) {
        request += "&";
        let num = parseInt(this.num_songs);
        if (num > 50) this.num_songs = "50";
        request += "num=" + (num < 1 ? "1" : this.num_songs);
      }
      this.request = request;
    },
    submitRequest() {
      if (!this.artists) {
        this.$router.push("404.html");
        return;
      }
      window.location.href = this.request;
      // var https: any = require(https);
      // var response = https.get(this.request);
      // return response;
    },
  },
});
